













































import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class Home extends Vue {
    @Prop() devices!: Array<any>;
    name = '';
    isEdit = false;
    editableDeviceId: any = {};

    get lowestRssi() {
        if (!this.devices) return [];
        return this.devices.sort((a, b) => (a.rssi < b.rssi ? 1 : -1)).slice(0, 10);
    }

    edit(device: any) {
        this.isEdit = true;
        this.editableDeviceId = device.id;
        this.name = device.name || 'Untitled...';
    }

    getDeviceById(id: string) {
        return this.devices.find((e: { id: any; }) => e.id === id);
    }

    save() {
        const match = this.getDeviceById(this.editableDeviceId);
        match.name = this.name.toString();
        this.name = '';
        this.isEdit = false;
    }

    exclude(device: any) {
        const match = this.getDeviceById(device.id);
        match.excluded = true;
    }

    add(device: any) {
        const match = this.getDeviceById(device.id);
        match.excluded = false;
    }
}
