



















import { Component, Vue, Watch } from 'vue-property-decorator';

declare global {
    interface Window {
        ble: any;
        cordova: any;
        plugins: any;
    }
}

@Component
export default class App extends Vue {
    allDevices: Array<any> = [];
    distance = 0;

    getStoredDistance() {
        const distance = localStorage.getItem('DISTANCE') || '0';
        this.distance = JSON.parse(distance);
    }

    getStoredDevices() {
        const devices = localStorage.getItem('DEVICES') || '[]';
        this.allDevices = JSON.parse(devices);
    }

    getStoredNotificationSettings() {
        const notification = localStorage.getItem('NOTIFICATION') || 'false';
        return JSON.parse(notification);
    }

    created() {
        this.getStoredDevices();
        this.getStoredDistance();
        document.addEventListener('deviceready', this.onDeviceReady, false);
    }

    startBackgroundNotifications() {

        window.cordova.plugins.notification.local.setDefaults({
            led: { color: '#FF00FF', on: 500, off: 500 },
            vibrate: true
        });

        if (!this.getStoredNotificationSettings()) {
            localStorage.setItem('NOTIFICATION', JSON.stringify(true));
            window.cordova.plugins.backgroundMode.on('activate', function () {
                window.cordova.plugins.notification.local.schedule({
                    id: 1,
                    title: 'Please activate notifications',
                    text: 'Testing notifications',
                    smallIcon: 'res://cordova',
                    sound: null,
                    badge: 1
                });
            });

            window.cordova.plugins.backgroundMode.on('deactivate', function () {
                window.cordova.plugins.notification.local.cancelAll(1);
            });
        }
    }

    sendPushNotification() {
        window.cordova.plugins.notification.local.schedule({
            id: 1,
            title: 'ALERT',
            text: 'Someone is too close',
            smallIcon: 'res://cordova',
            sound: null,
            badge: 1
        });
    }

    onDeviceReady() {
        window.cordova.plugins.backgroundMode.enable();
        this.startBackgroundNotifications();

        this.collectDevices();

        setInterval(() => {
            this.collectDevices();
        }, 500);

        setInterval(() => {
            this.cleanAlerts();
        }, 5000);
    }

    cleanAlerts() {
        this.allDevices.forEach(d => {
            d.isDanger = false;
        });
    }

    collectDevices() {
        window.ble.startScan([], this.onDiscoverDevice, this.onError);
    }

    onDiscoverDevice(d: any) {
        const match = this.allDevices.find(e => e.id === d.id);
        if (!match) {
            this.allDevices.push(d);
        } else {
            match.rssi = d.rssi;
            match.distance = d.rssi * -2.5;
            match.isDanger = this.shouldAlert(d.rssi);

            if (match.isDanger && !match.excluded) {
                navigator.vibrate(200);
                this.sendPushNotification();
            }
        }
    }

    shouldAlert(rssi: number) {
        let distance = -60;
        if (this.distance === 1) {
            distance = -80;
        }
        if (this.distance === 2) {
            distance = -90;
        }
        if (this.distance === 3) {
            distance = -100;
        }
        return rssi > distance;
    }

    onError(e: any) {
        console.log('Error trying to get device', e);
    }

    @Watch('allDevices')
    onDevicesChange(d: any) {
        localStorage.setItem('DEVICES', JSON.stringify(d));
    }

    changeDistance(distance: number) {
        this.distance = distance;
        localStorage.setItem('DISTANCE', JSON.stringify(distance));
    }
}
